.wrapper {
  display: flex;
  flex-direction: column;
  background-color: #f4fbff;
  flex: 1;
  padding: 40px 50px;
}

.project-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.project-name {
  font-size: 30px;
  color: #163147;
}

.todo {
  display: flex;
  justify-content: space-between;
}

.left-side, .right-side {
  display: flex;
  gap: 10px;
  font-size: 20px;
  align-items: center;
}

.left-side input {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  appearance: none;
  border: 2px solid rgb(90, 90, 90);
  position: relative;
  cursor: pointer;
}

.due-date {
  margin-top: 4px;
  font-size: 20px;
}

.left-side input:checked::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #496f8f;
}

.left-side input:checked + .todo-title {
  text-decoration: line-through;
  color: rgb(90, 90, 90);
}

.todos {
  display: flex;
  flex-direction: column;
gap: 16px;
}

.del-btn {
  border: none;
  background-color: #f4fbff;
}

.details {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 30px;
  border: 1px solid #37393A;
  padding: 14px 10px;
  border-radius: 5px;
}

.details .l, .details .r {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.details b {
  color: #37393A;
}

.wrap {
  display: flex;
  flex-direction: column;
  gap: 10px;
}`, "",{"version":3,"sources":["webpack://./src/project-content.css"],"names":[],"mappings":";AACA;EACE,aAAa;EACb,sBAAsB;EACtB,yBAAyB;EACzB,OAAO;EACP,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,eAAe;EACf,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,SAAS;EACT,eAAe;EACf,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,gBAAgB;EAChB,iCAAiC;EACjC,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,yBAAyB;AAC3B;;AAEA;EACE,6BAA6B;EAC7B,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,sBAAsB;AACxB,SAAS;AACT;;AAEA;EACE,YAAY;EACZ,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,SAAS;EACT,yBAAyB;EACzB,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,QAAQ;AACV;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX","sourcesContent":["\r\n.wrapper {\r\n  display: flex;\r\n  flex-direction: column;\r\n  background-color: #f4fbff;\r\n  flex: 1;\r\n  padding: 40px 50px;\r\n}\r\n\r\n.project-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 20px;\r\n}\r\n\r\n.project-name {\r\n  font-size: 30px;\r\n  color: #163147;\r\n}\r\n\r\n.todo {\r\n  display: flex;\r\n  justify-content: space-between;\r\n}\r\n\r\n.left-side, .right-side {\r\n  display: flex;\r\n  gap: 10px;\r\n  font-size: 20px;\r\n  align-items: center;\r\n}\r\n\r\n.left-side input {\r\n  width: 24px;\r\n  height: 24px;\r\n  border-radius: 50%;\r\n  appearance: none;\r\n  border: 2px solid rgb(90, 90, 90);\r\n  position: relative;\r\n  cursor: pointer;\r\n}\r\n\r\n.due-date {\r\n  margin-top: 4px;\r\n  font-size: 20px;\r\n}\r\n\r\n.left-side input:checked::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  top: 2px;\r\n  left: 2px;\r\n  width: 16px;\r\n  height: 16px;\r\n  border-radius: 50%;\r\n  background-color: #496f8f;\r\n}\r\n\r\n.left-side input:checked + .todo-title {\r\n  text-decoration: line-through;\r\n  color: rgb(90, 90, 90);\r\n}\r\n\r\n.