___CSS_LOADER_EXPORT___.push([module.id, `/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input, button, textarea, select {
  font: inherit;
}

/* 8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}

body {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

* {
  font-family: "Nunito", sans-serif;
}

.logo {
  display: flex;
  gap: 10px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #37393A;
  color: #E8EEF2;
  padding: 16px 26px;
  font-size: 24px;
}

.add-todo-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  border: none;
  border-radius: 10px;
  background-color: #e2eaee;
  transition: background-color .3s ease;
  padding: 8px 14px;
  font-size: 20px;
  font-weight: 700;
  color: #163147;
}

.add-todo-btn:hover {
  background-color: #cbd6da;
}

.sidebar {
  display: flex;
  flex-direction: column;
  background-color: #C7D3DD;
  width: clamp(200px, 20vw, 100%);
  padding: 20px 30px;
  gap: 6px;
}

.projects {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.project {
  display: flex;
}

.project-title {
  font-size: 18px;
}

.main {
  display: flex;
  flex: 1;
}

.projects h2 {
  color: #37393A;
  margin-bottom: 10px;
}

.projects button, .add-project-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  border: none;
  border-radius: 10px;
  flex: 1;
  background-color: #C7D3DD;
  color: #37393A;
  transition: background-color .3s ease;
  padding: 8px;
}

.projects button:hover, .add-project-btn:hover {
  background-color: #bac4ca;
}

.add-project-btn {
  display: flex;
  height: 30px;
  padding: 26px 8px;
  flex: 0;
  font-weight: 700;
  color: #5785ac;
  font-size: 18px;
}

footer {
  background-color: #37393A;
  color: #E8EEF2;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
}


.wrapper {
  display: flex;
  flex-direction: column;
  background-color: #f4fbff;
  flex: 1;
  padding: 40px 50px;
}

.project-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.project-name {
  font-size: 30px;
  color: #163147;
}

.todo {
  display: flex;
  justify-content: space-between;
}

.left-side, .right-side {
  display: flex;
  gap: 10px;
  font-size: 20px;
}

.left-side input {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  appearance: none;
  border: 2px solid rgb(90, 90, 90);
  position: relative;
  cursor: pointer;
}

.left-side input:checked::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #77B6EA;
}

.left-side input:checked + .todo-title {
  text-decoration: line-through;
  color: rgb(90, 90, 90);
}

.project-btn.active {
  background-color: #bac4ca;
}

.del-btn {
  margin-left: auto;
}


.del-btn {
  background-color: transparent !important;
}
