___CSS_LOADER_EXPORT___.push([module.id, `.add-project-dialog {
    border: none;
    margin: auto;
    border-radius: 10px;
    width: 500px;
    height: 200px;
}

.add-project-form {
    display: flex;
    flex-direction: column;
    gap: 14px;
}

.add-project-form .form-group {
    display: flex;
    flex-direction: column;
}

.add-project-form .form-group input {
    height: 34px;
}

.add-project-form legend, .add-todo-form legend {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: -8px;
}

.add-project-form label {
    font-size: 18px;
}

.btns {
    display: flex;
    gap: 6px;
    justify-content: end;
}

.btns .submit-btn {
    background-color: #37393A;
    color: #e2eaee;
}

.btns .add-project-cancel-btn, .btns .add-todo-cancel-btn {
    background-color: white;
    border: 1px solid #37393A;
}

.btns * {
    border: none;
    padding: 8px;
    border-radius: 5px;
}

.add-todo-dialog {
    border: none;
    margin: auto;
    border-radius: 10px;
    width: 500px;
    height: 356px;
}

.add-todo-form {
    display: flex;
    flex-direction: column;
    gap: 14px;
}

.add-todo-dialog .container {
    display: flex;
    justify-content: space-between;
}

.add-todo-form .left, .add-todo-form .right {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.add-todo-form .form-group {
    display: flex;
    flex-direction: column;
}

.add-todo-form .form-group label {
    font-size: 18px;
}

.add-todo-form input, .add-todo-form select {
    width: 224px;
    height: 30px;
}

.add-todo-form textarea {
    height: 106px;
}`, "",{"version":3,"sources":["webpack://./src/dialog.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,uBAAuB;IACvB,yBAAyB;AAC7B;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB","sourcesContent":[".add-project-dialog {\r\n    border: none;\r\n    margin: auto;\r\n    border-radius: 10px;\r\n    width: 500px;\r\n    height: 200px;\r\n}\r\n\r\n.add-project-form {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 14px;\r\n}\r\n\r\n.add-project-form .form-group {\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.add-project-form .form-group input {\r\n    height: 34px;\r\n}\r\n\r\n.add-project-form legend, .add-todo-form legend {\r\n    font-weight: 700;\r\n    font-size: 30px;\r\n    margin-bottom: -8px;\r\n}\r\n\r\n.add-project-form label {\r\n    font-size: 18px;\r\n}\r\n\r\n.btns {\r\n    display: flex;\r\n    gap: 6px;\r\n    justify-content: end;\r\n}\r\n\r\n.btns .submit-btn {\r\n    background-color: #37393A;\r\n    color: #e2eaee;\r\n}\r\n\r\n.btns .add-project-cancel-btn, .btns .add-todo-cancel-btn {\r\n    background-color: white;\r\n    border: 1px solid #37393A;\r\n}\r\n\r\n.btns * {\r\n    border: none;\r\n    padding: 8px;\r\n    border-radius: 5px;\r\n}\r\n\r\n.add-todo-dialog {\r\n    border: none;\r\n    margin: auto;\r\n    border-radius: 10px;\r\n    width: 500px;\r\n    height: 356px;\r\n}\r\n\r\n.add-todo-form {\r\n    display: flex